'use client';

import { FormEvent } from 'react';
import { Plus, Loader2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
    Select, SelectContent, SelectItem, SelectTrigger, SelectValue,
} from "@/components/ui/select";
import { BahanBaku } from '@/hooks/admin/useManajemenResepPage';

interface ResepFormCardProps {
    bahanBakuList: BahanBaku[];
    selectedBahan: string;
    setSelectedBahan: (val: string) => void;
    jumlah: string;
    setJumlah: (val: string) => void;
    isAdding: boolean;
    onAdd: (e: FormEvent) => void;
}

export default function ResepFormCard({
    bahanBakuList,
    selectedBahan,
    setSelectedBahan,
    jumlah,
    setJumlah,
    isAdding,
    onAdd
}: ResepFormCardProps) {

    return (
        <Card className="lg:col-span-1 h-fit shadow-md border-t-4 border-t-[#D93F21]">
            <CardHeader>
                <CardTitle className="text-lg">Tambah Komposisi</CardTitle>
            </CardHeader>
            <CardContent>
                <form onSubmit={onAdd} className="space-y-4">
                    <div className="space-y-2">
                        <Label>Pilih Bahan Baku</Label>
                        <Select value={selectedBahan} onValueChange={setSelectedBahan}>
                            <SelectTrigger>
                                <SelectValue placeholder="Cari bahan..." />
                            </SelectTrigger>
                            <SelectContent>
                                {bahanBakuList.map((bahan) => (
                                    <SelectItem key={bahan.id} value={bahan.id.toString()}>
                                        {bahan.nama_bahan_baku} ({bahan.satuan?.nama_satuan})
                                    </SelectItem>
                                ))}
                            </SelectContent>
                        </Select>
                    </div>

                    <div className="space-y-2">
                        <Label>Jumlah Dibutuhkan</Label>
                        <div className="flex items-center gap-2">
                            <Input 
                                type="number" 
                                placeholder="0" 
                                value={jumlah}
                                onChange={e => setJumlah(e.target.value)}
                                required
                                step="0.01"
                                className="flex-1"
                            />
                            <span className="text-sm font-medium text-gray-500 bg-gray-100 px-3 py-2 rounded-md min-w-[60px] text-center">
                                {bahanBakuList.find(b => b.id.toString() === selectedBahan)?.satuan?.nama_satuan || '-'}
                            </span>
                        </div>
                        <p className="text-xs text-gray-400">
                            Contoh: Jika Nasi Goreng butuh 100gr beras, isi 100.
                        </p>
                    </div>

                    <Button type="submit" disabled={isAdding} className="w-full bg-[#D93F21] hover:bg-[#b9351b]">
                        {isAdding ? <Loader2 className="h-4 w-4 animate-spin" /> : <><Plus className="h-4 w-4 mr-2" /> Tambahkan ke Resep</>}
                    </Button>
                </form>
            </CardContent>
        </Card>
    );
}