'use client';

import Link from 'next/link';
import { ArrowLeft } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { ProdukDetail } from '@/hooks/admin/useManajemenResepPage';

interface ResepHeaderProps {
    produk: ProdukDetail;
}

export default function ResepHeader({ produk }: ResepHeaderProps) {
    return (
        <div className="flex items-center gap-4">
            <Link href="/admin/produk-resto">
                <Button variant="outline" size="icon">
                    <ArrowLeft className="h-4 w-4" />
                </Button>
            </Link>
            <div>
                <h1 className="text-2xl font-bold text-gray-900">Resep: {produk.nama_produk}</h1>
                <p className="text-gray-500 text-sm">
                    Tentukan bahan baku untuk 1 {produk.satuan?.nama_satuan} {produk.nama_produk}.
                </p>
            </div>
        </div>
    );
}