'use client';

// Import Custom Hook untuk Logika
import { useManajemenResepPage } from '@/hooks/admin/useManajemenResepPage';

// Import Komponen UI Pecahan
import ResepHeader from '@/components/admin/resep/ResepHeader';
import ResepFormCard from '@/components/admin/resep/ResepFormCard';
import ResepTableCard from '@/components/admin/resep/ResepTableCard';

export default function ManajemenResepPage() {
    // 1. Panggil logika dari Custom Hook
    const {
        // Data Utama
        produk,
        resepList,
        bahanBakuList,
        loading,
        
        // State Form (untuk input tambah bahan)
        selectedBahan,
        setSelectedBahan,
        jumlah,
        setJumlah,
        isAdding,

        // Actions
        handleAddBahan,
        handleDeleteItem
    } = useManajemenResepPage();

    // 2. Handle Loading State
    if (loading) {
        return (
            <div className="flex items-center justify-center min-h-[50vh] text-gray-500">
                Memuat data resep...
            </div>
        );
    }

    // 3. Handle Error / Not Found
    if (!produk) {
        return (
            <div className="flex items-center justify-center min-h-[50vh] text-red-500 font-bold">
                Produk tidak ditemukan.
            </div>
        );
    }

    // 4. Render Halaman Utama
    return (
        <div className="space-y-6 max-w-5xl mx-auto pb-20">
            
            {/* Header: Judul Produk & Tombol Kembali */}
            <ResepHeader produk={produk} />

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                
                {/* KIRI: Form Tambah Bahan Baku */}
                <ResepFormCard 
                    bahanBakuList={bahanBakuList}
                    selectedBahan={selectedBahan}
                    setSelectedBahan={setSelectedBahan}
                    jumlah={jumlah}
                    setJumlah={setJumlah}
                    isAdding={isAdding}
                    onAdd={handleAddBahan}
                />

                {/* KANAN: Tabel Daftar Resep (Menggunakan komponen yang Anda pilih) */}
                <ResepTableCard 
                    resepList={resepList}
                    onDelete={handleDeleteItem}
                />
            </div>
        </div>
    );
}