'use client';

import { Trash2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
    Table, TableBody, TableCell, TableHead, TableHeader, TableRow,
} from '@/components/ui/table';
import { ResepItem } from '@/hooks/admin/useManajemenResepPage';

interface ResepTableCardProps {
    resepList: ResepItem[];
    onDelete: (id: number) => void;
}

export default function ResepTableCard({ resepList, onDelete }: ResepTableCardProps) {
    return (
        <Card className="lg:col-span-2 shadow-sm">
            <CardHeader>
                <CardTitle className="text-lg">Komposisi Saat Ini</CardTitle>
            </CardHeader>
            <CardContent>
                <div className="rounded-md border">
                    <Table>
                        <TableHeader>
                            <TableRow className="bg-gray-50">
                                <TableHead>Nama Bahan</TableHead>
                                <TableHead>Jumlah</TableHead>
                                <TableHead>Satuan</TableHead>
                                <TableHead className="text-right">Aksi</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {resepList.length === 0 ? (
                                <TableRow>
                                    <TableCell colSpan={4} className="h-32 text-center text-gray-500">
                                        Belum ada komposisi resep.
                                    </TableCell>
                                </TableRow>
                            ) : (
                                resepList.map((item) => (
                                    <TableRow key={item.id}>
                                        <TableCell className="font-medium">
                                            {item.bahan_baku?.nama_bahan_baku}
                                        </TableCell>
                                        <TableCell>
                                            {Number(item.jumlah)}
                                        </TableCell>
                                        <TableCell>
                                            <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-blue-50 text-blue-700">
                                                {item.bahan_baku?.satuan?.nama_satuan}
                                            </span>
                                        </TableCell>
                                        <TableCell className="text-right">
                                            <Button 
                                                variant="ghost" 
                                                size="icon" 
                                                className="h-8 w-8 text-red-600 hover:bg-red-50"
                                                onClick={() => onDelete(item.id)}
                                            >
                                                <Trash2 className="h-4 w-4" />
                                            </Button>
                                        </TableCell>
                                    </TableRow>
                                ))
                            )}
                        </TableBody>
                    </Table>
                </div>
            </CardContent>
        </Card>
    );
}