'use client';

import { useState, useEffect, FormEvent } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { useParams } from 'next/navigation';

// Definisi Tipe Data
export interface Satuan { nama_satuan: string; }
export interface BahanBaku {
    id: number;
    nama_bahan_baku: string;
    satuan: Satuan;
}
export interface ResepItem {
    id: number;
    bahan_baku_id: number;
    jumlah: number;
    keterangan: string;
    bahan_baku: BahanBaku;
}
export interface ProdukDetail {
    id: number;
    nama_produk: string;
    harga_jual: number;
    satuan: Satuan;
}

export function useManajemenResepPage() {
    const params = useParams();
    const produkId = params.id; 
    
    // State Data
    const [produk, setProduk] = useState<ProdukDetail | null>(null);
    const [resepList, setResepList] = useState<ResepItem[]>([]);
    const [bahanBakuList, setBahanBakuList] = useState<BahanBaku[]>([]);
    const [loading, setLoading] = useState(true);

    // State Form
    const [selectedBahan, setSelectedBahan] = useState('');
    const [jumlah, setJumlah] = useState('');
    const [isAdding, setIsAdding] = useState(false);

    // 1. Fetch Data
    const fetchData = async () => {
        setLoading(true);
        try {
            const [resProduk, resResep, resBahan] = await Promise.all([
                axios.get(`/api/produkRestos/${produkId}`),
                axios.get(`/api/produkRestos/${produkId}/resep`),
                axios.get(`/api/bahanBakus`)
            ]);

            setProduk(resProduk.data);
            setResepList(resResep.data);
            setBahanBakuList(resBahan.data);
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat data resep.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        if (produkId) fetchData();
    }, [produkId]);

    // 2. Handlers
    const handleAddBahan = async (e?: FormEvent) => {
        if (e) e.preventDefault();
        
        if (!selectedBahan || !jumlah) {
            toast.error("Pilih bahan dan isi jumlah!");
            return;
        }

        setIsAdding(true);
        try {
            await axios.post(`/api/produkRestos/${produkId}/resep`, {
                bahan_baku_id: selectedBahan,
                jumlah: jumlah,
                keterangan: '-'
            });

            toast.success("Bahan berhasil ditambahkan!");
            setJumlah(''); 
            
            // Refresh list resep tanpa reload semua
            const resNewResep = await axios.get(`/api/produkRestos/${produkId}/resep`);
            setResepList(resNewResep.data);

        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal menambah bahan.");
        } finally {
            setIsAdding(false);
        }
    };

    const handleDeleteItem = async (resepId: number) => {
        if (!confirm("Hapus bahan ini dari resep?")) return;

        try {
            await axios.delete(`/api/reseps/${resepId}`);
            toast.success("Bahan dihapus dari resep.");
            setResepList(prev => prev.filter(item => item.id !== resepId));
        } catch (error) {
            toast.error("Gagal menghapus item.");
        }
    };

    return {
        // Data
        produk,
        resepList,
        bahanBakuList,
        loading,
        
        // Form State
        selectedBahan,
        setSelectedBahan,
        jumlah,
        setJumlah,
        isAdding,

        // Actions
        handleAddBahan,
        handleDeleteItem
    };
}